
import showMore from '@/components/ShowMore/showMore' 
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
XHeader,
InlineLoading,
showMore
},
data() {
return {
locationName: '', //标题
dataobj: {}
};
},
mounted() {
this.locationName = this.$route.params.name;
this.initdata();
},
methods: {
initdata() {
this.$ajax.post(this.$root.host, {
'MSH.1': 'zhibiao',
'MSH.2': 'getZhiBiao',
'cbm': this.$route.params.code,
'token':this.$root.loginInfo.token,
}).then(data => {
console.log(data)
if (typeof data != 'undefined' && Object.keys(data).length > 0) { //判断是否空对象

this.dataobj = data;
}
})
},
}
};



